<template>
  <div class="row">
    <div class="col-sm-3">

      <h5 class="fw-none text-accent">其他信息</h5>
      <listing-member-face-card :is_icon="isPlus" :the_member="membery" v-show="membery!=null" @changeSignal="changeMember"></listing-member-face-card>
      <div v-show="membery==null" class="member-loading-wrapper">
        <loading-middle></loading-middle>
      </div>

    </div>
    <div class="col-sm-3 pt-4">
      
      <div class="fs-14 timed-wrapper">
        <simple-input 
          @returnValue="reciverNumed" 
          :the_name="'prices_numd'"
          :the_tip_header="`報價單號`"
          :the_placeholder="`- -`"
          :the_min_length="3"
          :the_max_length="12"

          ref="numRef"
          >
          <div slot="input_txt">報價單號：</div>
        </simple-input>
      </div>
      <div class="fs-14 timed-wrapper">
        <simple-input 
          @returnValue="reciverTimed" 
          :the_name="'prices_timed'"
          :the_tip_header="`報價時間`"
          :the_placeholder="`yyyy-mm-dd`"

          ref="timedRef"
          >
          <div slot="input_txt">報價日期：</div>
        </simple-input>
      </div>

    </div>
    <div class="col-sm-3 flex-center">
      
    </div>
    <div class="col-sm-1 flex-center">
      <button @click="_pricesSubmit" id="pricesResBtn" class="hidden"></button>
    </div>
    <div class="col-sm-2 pt-2">
      
      <button class="btn btn-light btn-block btn-sm" 
        @click="addIntoFreight">
        <span class="fs-16">+</span>
        加入產品
      </button>

      <button class="btn btn-outline-info btn-block btn-sm"
        @click="pricesPdfView">下一步</button>

    </div>
  </div>
</template>

<script>
import tool from '@/common/es/tool'

import SimpleInput from '@/components/Input/Simple/SimpleInput.vue'
import ListingMemberFaceCard from '@/components/Card/ListingMemberFaceCard.vue'
import LoadingMiddle from '@/components/Shimmer/LoadingMiddle.vue'

import conn from '@/service/conn'

export default {
  name: 'PricesPlusFooter',
  components: {
    SimpleInput,
    ListingMemberFaceCard,
    LoadingMiddle
  },
  props: {
    membery: {
      type: Object
    },
    isPlus: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      timed_prices: '',
      numed_prices: ''
    }
  },
  methods: {
    _pricesSubmit() {
      this.$emit('pricesSubmit_Father')
    },
    pricesSubmit() {
      tool.click('pricesResBtn')
    },

    pricesPdfView() {
      this.$emit('pricesPdfView_Father')
    },
    reciverTimed(v) {
      this.timed_prices = v
      this.$emit('reciverTimed_Father', this.timed_prices)
    },
    reciverNumed(v) {
      this.numed_prices = v

      conn.prices.getOneByAny({
        num: v
      }).then(res => {
        if (conn.judge_response(res[0])) {

          this.$emit('reciverNumed_Father', '')
          tool.showToast('報價單號“' + v + '”重復，數據庫中已有相同單號的報價單！')
          
        } else {
          this.$emit('reciverNumed_Father', this.numed_prices)
        }
      })

    },
    changeMember() {
      this.$emit('changeMember_Father')
    },
    addIntoFreight() {
      tool.showModal('insertFreightModal')
    },
    locationData(num, timed) {
      this.$refs.numRef.locationData(num)
      this.$refs.timedRef.locationData(timed)
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/content.sass"

.timed-wrapper
  display: inline-block
  line-height: 1
  height: 24px

.member-loading-wrapper
  height: 70%
</style>


